  function settingsUpdate() {
   
    if (validator.isEmail(adminEmail)) {
      setEmailError(false)
      setConfigLoading(true)
      let payloadSettings = {
        admin_email: adminEmail,
        visited_pline_default_expiry: rangeValue,
      }
      try {
        fetch(`${mainConfig.MAIN_ENDPOINT}/api/update-settings`, {
          method: 'post',
          headers: {
            Authorization: `Bearer ${token}`,
            Accept: 'application/json',
            'Content-Type': 'application/json',
          },
          body: JSON.stringify(payloadSettings),
        })
          .then(function (response) {
            if (response.status != 200) {
              return
            }
            response.json().then(function (data) {
              message.success(data.message)
              setConfigLoading(false)
            })
          })
          .catch(function (err) {})
      } catch (err) {
        console.log('ERR>>>', err)
      }
    } else {
      setEmailError(true)
    }
  }
  function getSettings() {
    setLoading(true)
    try {
      fetch(`${mainConfig.MAIN_ENDPOINT}/api/settings`, {
        method: 'get',
        headers: {
          Authorization: `Bearer ${token}`,
          Accept: 'application/json',
          'Content-Type': 'application/json',
        },
      })
        .then(function (response) {
          if (response.status != 200) {
            return
          }
          response.json().then(function (data) {
            let arr = new Array()

            data.data.settings.map((value: any) => {
              arr.push(value)
            })

            setsettingsOne(arr[0])
            setsettingsTwo(arr[1])
            setadminEmail(data.data.settings[0].meta_value)
            setrangeValue(data.data.settings[1].meta_value)
            setLoading(false)
          })
        })
        .catch(function (err) {})
    } catch (err) {
      console.log('ERR>>>', err)
    }
   
  }
